import React from 'react';
import { Caption } from 'external/src/typography';
import type { CaptionFontColor } from 'external/src/typography';
import { InputHintProps } from 'shared/types/input';

const InputHint: React.FC<InputHintProps> = ({
  disabled,
  isError,
  dataTestId,
  hintMessage,
  hintColor = 'tass_secondary'
}): JSX.Element => {
  let textColor: CaptionFontColor = hintColor;

  if (isError) { textColor = 'negative_dark'; }

  if (disabled) { textColor = 'secondary'; }

  return (
    <Caption
      dataTestId={ dataTestId }
      color={ textColor }
      inline
    >
      { hintMessage }
    </Caption>
  );
};

const MemoizedInputHint = React.memo(InputHint);

export { MemoizedInputHint };
